
// NAVIGATION
.main-header,
.nav {
	display: flex;
	flex-direction: column;
}

nav {
	a#pull {
		color: $io-black;
	}
	a#pull:link {
		color: $io-black !important;
	}
}

nav {
	.nav {
		display: none;
	}
}
.main-header {
	padding-top: 2em;
	padding-left: 1em;
	padding-right: 1em;
	padding-bottom: 2em;
	background: $off-white;
	// width: 100vw;
	// z-index: 2000;
	.site-logo {
		width: 50px;
		align-self: center;
	}
	.nav {
		margin-top: 1.5em;
		margin-bottom: 0;
		list-style: none;
		margin-left: 0;
		padding-left: 0;
		padding-top: 0;
		padding: 0;
		align-self: center;
		li {
			width: 100%;
			display: block;
			font-size: 0.8em;
			font-weight: 300;
			text-align: center;
			padding: .6em;

			button {

				&:hover {
					color: #0b0b0b;
					border-bottom: #52bab3;
					border-bottom: 1px solid #52bab3;
				}
				a {
					color: $io-black;
					text-decoration: none;
				}
			}	
		}

	}

}

/* -------------------------------
----  MEDIA QUERIES
---------------------------------- */

@media (min-width: 769px) {
	nav {
		display: flex;
		align-self: center;
		.nav {
			display: flex;
			align-self: center;
		}
	}
	.main-header {
		padding-top: 1em;
		padding-bottom: 1em;
		height: 150px;
		justify-content: flex-end;
		.site-logo {
			width: 60px;
		}
		.nav {
			flex-direction: row;
			justify-content: space-between;
			// padding-bottom: 2em;
			li {
				border-bottom-color:transparent;
				font-size: 0.9em;
			    padding-bottom: 0;
			}
		}
	}

	nav {
		a#pull {
			display: none;
		}
}


}
@media (min-width: 1025px) {
	.main-header {
		padding-top: 0;
		padding-bottom: 0;
		height: 80px;
		padding-top: 0;
		flex-direction: row;
		align-self: center;
		.site-logo {
			margin-right: auto;
			width: 30px;
		}
		.nav {
			padding-top: 0;
			padding-bottom: 0;
			margin-top: 0;
			margin-bottom: 0;
			align-self: center;
			li {
				margin-left: 0.85em;
				margin-right: 0.85em;
			}
		}
	}
}

